<script>
  import { units } from "../../constants/appConstant";
  import { conventionNaming } from "../../utils/utils";

  export default {
    props: ['onSave', 'onCancel', 'item'],
    data: () => ({
      conventionNaming: conventionNaming,
      rules: { 
        required: (value) => !!value || "Required.",
        numberRule: v  => !isNaN(parseFloat(v)) || "Required.",
      },
      expansionData: 0,
      valid: false,
      units,
      defaultItem:{
        object_type: 'analogOutput',
        object_name: 'point-1',
        address: 1,
        units: 'noUnits',
        description: 'na',
        relinquish_default: 1,
        event_state: 'normal',
        priority_array_write: {}
      }
    }),
    computed: {
      formData () {
        return this.item ? { ...this.item } : this.defaultItem;
      },
      setting() {
        return this.$store.state.setting;
      },
    },
    methods:{
      handleSave(){
        if(this.$refs.form.validate()){
          this.onSave(this.formData);
        }
      }
    },
  };
</script>
