                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)"> mdi-delete </v-icon>
                  <!-- <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item.uuid, 'uuid')">mdi-alert-octagram-outline</v-icon> -->
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" v-if="isClickSaveImport" @click="onSave(item)"  :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.object_name="{ item, index }">
                  <v-text-field
                    v-model="item.object_name" 
                    style="width: 100px"
                    required 
                    @input="(val)=>importArray[index].object_name = conventionNaming(val, setting)"
                    :rules="[() => !isExistingvalue(item.object_name, 'object_name') || 'Name Existed', rules.required]" 
                    :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" />
                </template>
                <template v-slot:item.address="{ item }">
                  <v-text-field 
                    v-model="item.address" 
                    type="number" 
                    style="width: 100px" 
                    required 
                    :rules="[() => !isExistingvalue(item.address, 'address', item) || 'Address Existed', rules.required]" 
                    :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" />
                </template>
                <template v-slot:item.description="{ item }">
                  <v-text-field v-model="item.description" style="width:250px" :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" />
                </template>
                <template v-slot:item.ts="{ item }" >
                  {{item.ts | timezone}}
                </template>
                <template v-slot:item.priority_array_write="{ item }">
                  {{item.priority_array_write && typeof item.priority_array_write == 'object' ? JSON.stringify(item.priority_array_write) : item.priority_array_write}}
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>
    
    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>

    <!-- Create/edit dialog-->
