    </v-dialog>

    <!-- Point Write Dialog -->
    <point-write-dialog 
      v-if="dialogPointWrite"
      :is-open="dialogPointWrite" 
      :on-close="()=>dialogPointWrite = false" 
      :selected-point="editedItem"
      :api="'bac-net'"
      :response-from-dialog="(response)=>{}"
    ></point-write-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="350px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-select v-if="editField === 'object_type'" v-model="editAll[editField]" :items="['analogOutput', 'analogValue']" :label="toUpperTextKey(editField)" ></v-select>
          <v-select v-if="editField === 'units'" v-model="editAll[editField]" :items="units" :label="toUpperTextKey(editField)" ></v-select>
          <v-select v-if="editField === 'event_state'" v-model="editAll[editField]" :items="['normal', 'fault', 'offnormal', 'highLimit', 'lowLimit', 'lifeSafetyAlarm']" :label="toUpperTextKey(editField)" ></v-select>
          <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
          <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" min="0" :label="toUpperTextKey(editField)" type="number"></v-text-field>
          <v-text-field v-if="typeof editAll[editField] === 'string' && editField !== 'object_type' && editField !== 'units' && editField !== 'event_state'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
