<script>

import { exportCSV, isJsonString, copyToClipboard, exportExcel, exportJSON, convertPointsValueToString, toUpperTextKey, conventionNaming } from "../../utils/utils";
import axios from "../../axios";
import * as xlsx from "xlsx";
import BacNetPointForm from './BacNetPointForm.vue';
import _find from 'lodash/find';
import _forIn from 'lodash/forIn';
import _cloneDeep from 'lodash/cloneDeep';
import ConfirmDialog from '../Common/ConfirmDialog';
import PointWriteDialog from '../Common/PointWriteDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import { DATA_TABLE_PROPS, units } from '../../constants/appConstant';

export default {
  components: {
    BacNetPointForm, ConfirmDialog, PointWriteDialog, ImportFromExcelDialog,
  },
  data:()=>({
    toUpperTextKey: toUpperTextKey, 
    conventionNaming: conventionNaming, 
    rules: {
      required: (value) => !!value || 'Required.',
    },
    dialog: false,
    dialogPointWrite: false,
    dialogImportExcel: false,
    dialogImport: false, 
    dialogDelete: false,
    dialogMassEdit: false,
    headers: [
      { text: "Actions", value: "actions", width: '90px', sortable: false },
      { text: "Point Name", value: "object_name", width: '190px', sortable: true },
      { text: "Enable",value: "enable", width: '120px', sortable: true },
      { text: "Type", value: "object_type", width: '150px', sortable: true },
      { text: "Address", value: "address", width: '130px', sortable: true },
      { text: "Present Value ", value:"present_value", width: '160px', sortable: true },
      { text: "Present Value Last Update", value:"ts", width: '210px', sortable: true },
      { text: "Relinquish Default", value: "relinquish_default", width: '190px', sortable: true },
      { text: "COV", value: "cov", width: '100px', sortable: true },
      { text: "Description", value: "description", width: '150px', sortable: true },
      { text: "Data Offset", value: "data_offset", width: '140px', sortable: true },
      { text: "Data Round", value: "data_round", width: '150px', sortable: true },
      { text: "Event State",value: "event_state", width: '150px', sortable: true },
      { text: "Units", value: "units", width: '110px', sortable: true },
      { text: 'Priority Array Write', value: 'priority_array_write', width: '150px', sortable: false , groupable: false },
      { text: "Created On",value:"created_on", width: '120px', sortable: true },
      { text: "Updated On",value: "updated_on", width: '120px', sortable: true  },
      { text: "UUID", value: "uuid", width: '170px', sortable: true },
    ],
    selected: [],
    valid: false,
    importArray: [],
    dataImport: "",
    action: "",
    e1: 1,
    items: [],
    search:"",
    searchWizard:"",
    intervalId: null,
    isClickSaveImport: false,
    tableProps: {...DATA_TABLE_PROPS},
    editAll: {},
    editField: "",
    units,
  }),
  computed: {
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  async mounted() {
    this.getData();
    this.getItems();
  },
  methods: {
    getData() {
      this.getBacnetInfo();
    },
    editItem(item) {
      this.currentItem = item;
      this.dialog = true;
    },
    onCancel() {
      this.dialog = false;
    },
     async getBacnetInfo() {
      this.bacnetInfo = await axios.get(`bac-net/server-info`);
    },
    async getBacnetStatus() {
      this.bacnetServerStatus = await axios.get(`bac-net/server-status`);
    },
    async onBacNetSave(data) {
      await axios.patch(`bac-net/server-info`, data).then(r => {
        this.dialog = false;
        this.$toasted.show('Success');
        this.getBacnetInfo();
      })
    },
    async onSave(data, resolve, reject) {
      try {
        let postData = {...data};
        const method = data.uuid ? 'patch': 'post';
        let url = `bac-net/point`;
        postData.enable = data.enable ? data.enable : false;
        if(postData.use_next_available_address ) delete postData.use_next_available_address;
        if (data.uuid) {
          url += '/' + data.uuid;
          delete postData.uuid;
        }
        const response = await axios[method](url, postData);        
        if(response.notif= "Success"){
          this.dialog = false;
          // await this.getItems();
          this.filterItemsImport();
          this.$toasted.show("success");
        }
        if (resolve) {
          resolve();
        }
      } catch (e) {
        if (reject) {
          reject();
        }
      }

    },
    filterItemsImport(){
      if (this.importArray && this.importArray.length > 0) {
        this.items.forEach(item=>{
          const index = this.importArray.findIndex(n => n.object_name == item.object_name);
          if(index > -1){
            return Object.assign(this.importArray[index], item);
          }
        })
      }
    },
    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.items.indexOf(this.currentItem);
      this.items.splice(this.editedIndex, 1);
      await axios.delete(`bac-net/point/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    async getItems() {
      let points  = await axios.get( `bac-net/point` );
      this.items = points.map(item => {
        const keys = Object.keys(item.point_store);
        if (keys && keys.length > 0) {
          keys.forEach(key => {
            item = { ...item, [key]: item.point_store[key] };
          });
        }
        delete item.point_store;
        delete item.point_uuid;
        return item;
      });
    },
    async saveImport() {
      if(this.importArray && this.importArray.length > 0){
        if(this.$refs.form.validate()){
          this.isClickSaveImport = true;
          try{
            await this.createMutiPoints(this.importArray);
          }catch(ex){
          }finally{
            this.closeDialogImport();
            this.selected = [];
            this.getItems();
          }
        }
      }
    },
    async createMutiPoints(points) {
      const promises=[];
      for(const item of points) {
        if(item.priority_array_write && typeof item.priority_array_write == 'string'){
          item.priority_array_write = JSON.parse(item.priority_array_write);
        }
        if(!item.uuid) {
          promises.push(new Promise((resolve, reject) => this.onSave(item, resolve, reject)));
        }
      }
      await Promise.all(promises);
    },
    clickContinue(){
      if(!isJsonString(this.dataImport) ){
        return this.$toasted.show("incorrect data type!", { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(point=>{
        delete point.uuid;
        return point;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      const points = _cloneDeep(this.items);
      const index = this.items.indexOf(this.currentItem);
      if(index !== -1){ //when edit
        points.splice(index, 1);
      }
      if(fieldName === "address"){
        value = Number(value)
      }
      return _find(points, {[fieldName]: value}) != null;;
    },
    clickImportJson(){
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : "";
      this.e1 = 1;
    },
    convertExcelToJson(file){
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = (event)=>{
        let data = event.target.result;
        let workbook = xlsx.read(data,{type:"binary"});
        workbook.SheetNames.forEach(sheet => {
            let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
            const items = rowObject.map((i) => {
              for(let key in i){
                const newKey = key.trimRight("");
                const value = i[key];
                delete i[key];
                i = {...i, [newKey]: value}
              }
              return i;
            });
            this.dataImport = JSON.stringify(items);
            this.dialogImportExcel = false;
            this.dialogImport = true;
            this.e1 = 1;
        });
      }
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport= false;
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },
    clickDownload(array, type) {
      const data = convertPointsValueToString(JSON.parse(JSON.stringify(array)), ['use_next_available_address']);
      if (type === 'csv') {
        return exportCSV('points', data);
      }
      if (type === 'excel') {
        return exportExcel('points', data);
      }
      if (type === 'json') {
        return exportJSON('points', JSON.stringify(array));
      }
    },
    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    convertExportData(array){
      const data = _cloneDeep(array).map((point)=>{
        _forIn(point, (point_value, point_key) => {
          if(point_key == 'point_store' || point_key == 'priority_array_write'){
            _forIn(point[point_key], (sub_value, sub_key) => {
              const subKey = `point.${point_key}.${sub_key}`;
              point = {...point, [subKey] : sub_value}
            });
            delete point[point_key];
          }
        });
        return point;
     });
      return data;
    },
    prepareToExit: function() {
      clearInterval(this.intervalId)
    },
    autoRefresh: function(){
      //set auto refresh
      if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
        this.intervalId = setInterval(()=>{
          this.getItems();
          if(this.setting.toastAutoRefresh){
            this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
          }
        }, this.setting.refreshTime);
      }
    },
    onClickRefresh(){
      this.getItems();
      this.selected = [];
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    openPointWriteDialog(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogPointWrite = true;
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.onSave(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editAll = { 
        enable: false, 
        object_type: 'analogOutput', 
        address: 0, 
        present_value: 0.0, 
        relinquish_default: 0, 
        cov: 0.0, 
        description: '', 
        data_offset: 0, 
        data_round: 0, 
        event_state: 'normal', 
        units : 'noUnits'
      }
      this.editField = fieldName;
    },
  }
}
</script>
